import React, { Component } from 'react'
import $ from 'jquery'
import anime from 'animejs'

let type

class Main extends Component {
  state = {
    date: {},
    isEnd: false
  }
  componentDidMount() {
    this.print()
    $('#autotype').autotype()
    setInterval(() => {
      this.time(2018, 11, 13)
    }, 1000)
    var audio = document.getElementById('audio')
    setTimeout(() => audio.play(), 5000)
    setTimeout(() => {
      this.end()
      clearTimeout(type) //type
      setTimeout(() => clearTimeout(type), 15000) //type
    }, 95000)
  }
  print = () => {
    $.fn.autotype = function() {
      var _this = $(this)
      var str = _this.html()
      // 
      str = str.replace(/(\s){2,}/g, '$1')
      var index = 0
      $(this).html('')
      var timer = function fn() {
        var current = str.slice(index, index + 1)
        // html,<p>
        if (current === '<') {
          index = str.indexOf('>', index) + 1
        } else {
          index++
        }
        //: setInterval_
        if (index < str.length - 1) {
          //2
          _this.html(str.substring(0, index) + (index & 1 ? '_' : ''))
        } else {
          _this.html(str.substring(0, index))
          clearTimeout(timer)
        }
        type = setTimeout(fn, 200)
      }
      // 1s
      setTimeout(timer, 1000)
    }
  }
  time = (year, month, day) => {
    var dateNow = new Date()
    var dateJNR = new Date(year, month - 1, day)
    // var anniversary = parseInt((dateNow - dateJNR) / (365*24*3600*1000))
    var d = parseInt((dateNow - dateJNR) / (24 * 3600 * 1000))
    var hour = parseInt(((dateNow - dateJNR) / (3600 * 1000)) % 24)
    var minute = parseInt(((dateNow - dateJNR) / (1000 * 60)) % 60)
    var second = parseInt(((dateNow - dateJNR) / 1000) % 60)
    this.setState({
      date: { d: d, hour: hour, minute: minute, second: second }
    })
  }

  end() {
    anime({
      targets: 'canvas',
      filter: 'grayscale(100%)'
    })
    setTimeout(() => {
      $('#conclusion').autotype()
      this.setState({ isEnd: true })
      anime({
        targets: '.date',
        right: 0,
        top: '50%',
        translateX: '-50%',
        translateY: '-50%',
        color: '#fff',
        scale: 2,
        easing: 'easeInBack'
      })

      anime({
        targets: '.mask',
        background: 'rgba(0, 0, 0, 0.8)',
        duration: 1500
      })
    }, 500)
  }
  render() {
    const date = () => {
      if (this.state.date.d !== undefined) {
        const { d, hour, minute, second } = this.state.date
        return (
          <p style={{ textShadow: this.state.isEnd ? '0 0 5px' : '' }}>
            : <span className="date-text">{d}</span> {' '}
            <span className="date-text">{hour}</span> {' '}
            <span className="date-text">{minute}</span> {' '}
            <span className="date-text">{second}</span> {' '}
          </p>
        )
      }
    }
    return (
      <div className="App animated bounceInLeft">
        <div className="date">{date()}</div>
        <div
          className="mask"
          style={{
            backgroundColor: this.state.isEnd
              ? 'rgba(0, 0, 0, 0.8)'
              : 'rgba(0, 0, 0, 0)'
          }}
        />
        <div id="autotype">
          <h1 style={{ fontWeight: 900, margin: '12px 0' }}>Hey</h1>
          <p></p>
          <p>
            
          </p>
          <p>
            {' '}
          </p>
          <p>
            
          </p>
          <p></p>
          <p></p>
          <p></p>
          <p></p>
          <div style={{ textAlign: 'right' }}>
            <p>2018-10-29</p>
          </div>
        </div>
        <p id="conclusion" style={{ letterSpacing: '-1px' }}>
          We are like dominoes. I fall for you. You fall for another.
        </p>
        <audio
          id="audio"
          src="http://www.170mv.com/kw/other.web.nf01.sycdn.kuwo.cn/resource/n3/21/93/1962163848.mp3"
        />
      </div>
    )
  }
}
export default Main



